import React from 'react';

//styles
import styles from './transactionHistory.module.css'

interface Props {
    items:Array<Item>
}

interface Item {
    id:string,
    type:string,
    amount:string,
    currency:string,
}

const TransactionHistory:React.FC<Props> = ({items}) => {
    
    return (
        <table className={styles.transaction_history}>
            <thead>
                <tr>
                <th>Type</th>
                <th>Amount</th>
                <th>Currency</th>
                </tr>
            </thead>

            <tbody>
                
                <tr>
                    <td>Invoice</td>
                    <td>125</td>
                    <td>USD</td>
                </tr>
            </tbody>
        </table>
    );
}

export default TransactionHistory;
